import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { motion, useAnimation, useViewportScroll } from "framer-motion";
import { Link, useMatch, useNavigate } from "react-router-dom";


const Nav = styled(motion.nav)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: 100vw;
  box-sizing: border-box;
  top: 0;
  font-size: 14px;
  padding: 20px 60px;
  color: white;
`;

const Col = styled.div`
  display: flex;
  align-items: center;
`;

const Logo = styled.div`
font-size: 30px;
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;700&display=swap');
font-family: 'Montserrat';
font-weight: 300;
margin-right: 20px;
margin-top: -5px;
span{
  font-weight: 700;
}
`;

const Items = styled.ul`
  display: flex;
  align-items: center;
  margin-top: 10px;
`;

const Item = styled.li`
  margin-right: 20px;
  color: ${(props) => props.theme.white.darker};
  transition: color 0.3s ease-in-out;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-size: 18px;
  &:hover {
    color: ${(props) => props.theme.white.lighter};
  }
`;

const Search = styled.form`
  color: white;
  display: flex;
  align-items: center;
  position: relative;
  left: -250px;
  svg {
    position: absolute;
    left: 190px;
    height: 25px;
    cursor: pointer;
  }
`;

const Circle = styled(motion.span)`
  position: absolute;
  width: 5px;
  height: 5px;
  border-radius: 4px;
  bottom: -8px;
  //중앙에 뭔가하고싶다면, left 0, right 0, margin 0 auto
  left: 0;
  right: 0;
  margin: 0 auto;
  background-color: ${(props) => props.theme.red};
`;
const Input = styled(motion.input)`
  transform-origin: right center;
  position: absolute;
  left: 0;
  padding: 5px 10px;
  padding-left: 30px;
  z-index: -1;
  color: white;
  font-size: 16px;
  background-color: transparent;
  border: 1px solid ${(props) => props.theme.white.lighter};
`;

const navVariants = {
    top : {
        backgroundColor: "rgba(0,0,0,0)",
    },
    scroll : {
        backgroundColor: "rgba(0,0,0,1)",
    },
}

function Header() {
  const navigate = useNavigate();
  const [value, setValue] = useState("");
  const onChange = (e:any) => {
    setValue(e.target.value);
  }
  const onSubmit = (e:any) => {
    e.preventDefault();
  if(value !== null){
    navigate(`/thecontents/search?keyword=${value}`);};
}
  const [searchOpen, setSearchOpen] = useState(false);
  //v5 useRoutMatch, v6 useMatch
  const homeMatch = useMatch("/thecontents");
  const tvMatch = useMatch("/thecontents/tv");
  const movieMatch = useMatch("/thecontents/movie");
  const inputAnimation = useAnimation();
  const navAnimation = useAnimation();
  const { scrollY } = useViewportScroll();
  const toggleSearch = () => {
    if (searchOpen) {
      //열려있으면 닫기
      inputAnimation.start({
        scaleX: 0,
      });
    } else {
      inputAnimation.start({ scaleX: 1 });
    }
    setSearchOpen((prev) => !prev);
  };
  
  useEffect(() => {
    scrollY.onChange(() => {
      if (scrollY.get() > 200) {
        navAnimation.start("scroll");
      } else {
        navAnimation.start("top");
      }
    });
  }, [scrollY,navAnimation]);
  return (
    <Nav
    variants={navVariants}
    animate={navAnimation} 
    initial="top">
      <Col>
        <Items>
          <Link to="/thecontents">
            <Item><Logo><span>The</span> Contents</Logo></Item>
          </Link>
          <Link to="/thecontents/tv">
            <Item>TV프로그램 {tvMatch && <Circle layoutId="redCircle" />}</Item>
          </Link>
          <Link to="/thecontents/movie">
            <Item>영화 { movieMatch && <Circle layoutId="redCircle" />}</Item>
          </Link>
        </Items>
      </Col>
      <Col>
        <Search  
        onSubmit={onSubmit}>
          <motion.svg
            onClick={toggleSearch}
            animate={{ x: searchOpen ? -185 : 0 }}
            transition={{ type: "linear" }}
            fill="currentColor"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z"
              clipRule="evenodd"
            ></path>
          </motion.svg>
          <Input
            onChange={onChange}
            value={value}
            animate={inputAnimation}
            initial={{ scaleX: 0 }}
            transition={{ type: "linear" }}
            placeholder="Search for movie or tv show."
          />
        </Search>
      </Col>
    </Nav>
  );
}

export default Header;
